@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
::-webkit-scrollbar{
    width: 5px;
    height: 6px;
  }
  
  ::-webkit-scrollbar-track{
    box-shadow: inset 0 0 5px #a5aaad;
    border-radius: 10px;
  }
  
  ::-webkit-scrollbar-thumb{
    background: #3ea175;
    border-radius: 10px;
  }
  
  ::-webkit-scrollbar-thumb:hover{
    background: #a5aaad;
  }
  
  *{
    margin: 0;
    padding: 0;
  }
  
  body{
    box-sizing: border-box;
  }

  
.text-primary-p{
    color: #a5aaad;
    font-size: 20px;
    font-weight: 700;
}

.font-bold{
    font-weight: 700;
}

.font-title{
    color: #2e4a66;
}

.text-lightblue{
    color: #469cac;
}

.text-red{
    color: #cc3d38;
}

.text-yellow{
    color: #a98921;
}

.text-green{
    color: #3b9668;
}

.container{
    display: grid;
    height: 100vh;
    grid-template-columns: 0.8fr 1fr 1fr 1fr;
    grid-template-rows: 0.2fr 3fr;
    grid-template-areas: 
    "sidebar nav nav nav"
    "sidebar main main main";
  }
  
  @media only screen and (max-width:978px) {
    .container{
      grid-template-columns: 1fr;
      grid-template-rows: 0.2fr 3fr;
      grid-template-areas: 
      "nav"
      "main";
    }
  }

.navbar {
    background-color: #FFF;
    grid-area: nav;
    display: flex;
    height: 60px;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px;
    border-bottom: 1px solid lightgray;
}

.navbar .nav-icon{
    display: none;
}

.navbar .nav-icon > i{
    font-size: 26px;
    color: #a5aaad;
}

.navbar .navbar-left > a{
    margin-right: 30px;
    text-decoration: none;
    color: #a5aaad;
    font-size: 15px;
    font-weight: 700;
}

.navbar .navbar-left .active-link{
    color: #265acc;
    border-bottom: 3px solid #265acc;
    padding-bottom: 12px;
}

.navbar .navbar-right{
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar .navbar-right > a{
    margin-left: 20px;
    text-decoration: none;
}

.navbar .navbar-right > a > i{
    color: #265acc;
    font-size: 16px;
    border-radius: 50px;
    background-color: #FFF;
    box-shadow: 2px 2px 5px #d9d9d9, -2px -2px 5px #FFF;
    padding: 7px;
}

@media only screen and (max-width: 978px){
    .navbar .nav-icon {
        display: inline;
        cursor: pointer;
    }
}

@media only screen and (max-width: 480px){
    .navbar-left{
        display: none;
    }
}
#sidebar{
    background: #020509;
    grid-area: sidebar;
    overflow-y: auto;
    padding: 20px;
    height: 100vh;
}

.sidebar-title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #f3f4f6;
    margin-bottom: 30px;
}

.sidebar-title > div > h1{
    font-size: 18px;
    display: inline;
}

.sidebar-title > i{
    font-size: 18px;
    display: none;
}

.sidebar-menu > h2{
    color: #3ea175;
    font-size: 18px;
    margin: 25px 0 15px;
    padding: 0 10px;
    font-weight: 800;
}

.sidebar-link{
    color: #f3f4f6;
    margin-bottom: 5px;
    padding: 10px;
}

.active-menu-link{
    background: rgba(62, 161, 112, 0.3);
    color: #3ea175 !important;
    padding: 10px;
    border-radius: 3px;
    margin-bottom: 5px;
    transition: all .5 ease-in-out;
}

.sidebar-link > .link{
    text-decoration: none;
    color: #a5aaad;
    font-weight: 700;
}

.sidebar-link .link i{
    margin-right: 10px;
    font-size: 18px;
}

.sidebar-logout{
    margin-top: 20px;
    padding: 10px;
    color: #e65061;
}

.sidebar-logout > a{
    text-decoration: none;
    color: #e65061;
    font-weight: 700;
    text-transform: uppercase;
}

.sidebar-logout > i{
    margin-right: 10px;
    font-size: 18px;
}

.sidebar-responsive{
    display: inline !important;
    margin: 0 !important;
    z-index: 9999 !important;
    left: 0 !important;
    position: absolute;
}

@media only screen and (max-width: 978px) {
    #sidebar{
        position: absolute;
        left: 0;
        margin: 0 0 0 -100%;
        transition: all 1s ease-in-out;
    }

    .sidebar-title > i{
        display: inline;
        cursor: pointer;
    }
}
main{
    background: #f3f4f6;
    grid-area: main;
    overflow-y: auto;
}

.main-container{
    padding: 20px 35px;
}

.main-title{
    display: flex;
    align-items: center;
}

.main-greeting > h1{
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
}

.main-greeting > p{
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;
}

.main-cards{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
    margin: 20px 0;
}

.card{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 70px;
    padding: 25px;
    border-radius: 5px;
    background: #fff;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #fff;
}

.card-inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 14px;
}

.card-inner > span{
    font-size: 25px;
}

.charts{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
    margin-top: 50px;
}

.charts-left,
.charts-right{
    padding: 25px;
    border-radius: 5px;
    background: #FFF;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #fff;
}

.charts-left-title,
.charts-right-title{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.charts-left-title > div > h1,
.charts-right-title > div > h1{
    font-size: 24px;
    color: #2e4a55;
    margin-bottom: 5px;
}

.charts-left-title > i,
.charts-right-title > i{
    color: #FFF;
    font-size: 20px;
    border-radius: 5px;
    padding: 15px;
    border: none;
}

.charts-left-title > i{
    background: #ffc100;
}

.charts-right-title > i{
    background: #39447a;
    border-radius: 200px 0px 200px 200px;
}

.charts-right-cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    gap: 20px;
    margin-top: 30px;
}

.card1, .card2, .card3, .card4{
    text-align: center;
    padding: 25px;
    border-radius: 5px;
    font-size: 14px;
}

.card1{
    background: #d1ecf1;
    color: #35a4ba;
}

.card2{
    background: #d2f9ee;
    color: #38e1b0;
}

.card3{
    background: #d6d6d6;
    color: #3a3e41;
}

.card4{
    background: #fddcdf;
    color: #f65a6f;
}

@media only screen and (max-width: 885px) {
    .main-cards{
        grid-template-columns: 1fr;
        grid-gap: 10px;
        gap: 10px;
        margin-bottom: 0;
    }

    .charts{
        grid-template-columns: 1fr;
        margin-top: 30px;
    }

}
